.onButton {
 margin-top: -40px;
 display: flex;
 justify-content: space-between;
 align-items: center;
 /* background-color: var(--secondarColor); */
 height: 25vh;
 border: 1px solid transparent;
 border-radius: 20px;
 transition: 0.3s;
}

.onButton:hover {
 background: white;
 .onButtonText {
   color: var(--mainColor);
 }
}

.onButtonText {
 font-size: 40px;
 color: var(--textColor);
 padding-left: 20px;
 text-transform: capitalize;
}

/* slider switch */
.switch {
 position: relative;
 display: inline-block;
 width: 40px;
 height: 20px;
 padding-right: 20px;
}

/* Hide default checkbox */
.switch input {
 opacity: 0;
 width: 0;
 height: 0;
}

/* Slider (round or rectangular part) */
.slider {
 position: absolute;
 cursor: pointer;
 top: 0;
 left: 0;
 right: 0;
 bottom: 0;
 background-color: #ccc;
 transition: .4s;
 border-radius: 34px; /* Rounded slider */
 padding-right: 20px;
}

/* Changing appearance when checked */
.slider:before {
 position: absolute;
 content: "";
 height: 12px;
 width: 12px;
 left: 4px;
 bottom: 4px;
 background-color: white;
 transition: .4s;
 border-radius: 50%; /* Rounded indicator */
 padding-right: 20px;
}

input:checked + .slider {
 background-color: #2196F3;
}

input:checked + .slider:before {
 transform: translateX(22px);
}
